html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}
h1,h2,h3,h4,h5,h6{
  margin: 0;
  padding: 0;
}
body {
  margin: 0;
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  font-size: 15px;
  color: rgba(0, 0, 0, 0.76);
  background: #fff;
  background-size: cover;
}

img {
  max-width: 100%;
}

a {
  color: inherit;
  text-decoration: none;
}

ul {
  list-style: none;
  padding: 0;
}

.is-outer {
  position: relative !important; // stylelint-disable-line declaration-no-important
  left: -9999px !important; // stylelint-disable-line declaration-no-important
}

.site-container {
  overflow: hidden;
}
.is-hidden {
  display: none !important; // stylelint-disable-line declaration-no-important
}

.for-mobile {
  display: none;

  @include mobile {
    display: block;
  }
}

.error {
  border: 2px #ff4019 solid !important; // stylelint-disable-line declaration-no-important
}

.btn-reset {
  border: none;
  background: transparent;
  cursor: pointer;
}
// CONTENT-CONTAINER====================================================================================================
.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1500px;
  background: #f5f5f5;
}
.container-full{
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1920px;
  background: #f5f5f5;
}
// =======================================================================================================
.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

// SITE-ELEMENTS==========================================================================================
.preloader{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.942);
  opacity: 1;
  visibility: visible;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 1s;
  z-index: 1000;
  & .loader{
    display: block;
  width: 200px;
  height: 200px;
  border-radius: 50%;
  border: 5px solid transparent;
  border-top-color: var(--color-text);
  position: relative;
  animation: rotating 2.5s infinite ease;
  -webkit-animation: rotating 2.5s infinite linear;
    &:after, &:before {
      content: "";
      position: absolute;
      border-radius: inherit;
      border: inherit;
    }
    &:after {
      top: 5px;
      left: 5px;
      width: 180px;
      height: 180px;
      border-top-color: var(--color-green);
      animation: rotating 2s infinite ease;
      -webkit-animation: rotating 2s infinite linear;
    }
      &:before {
      top: 15px;
      left: 15px;
      width: 160px;
      height: 160px;
      border-top-color: #3f5544;
      animation: rotating 1.5s infinite ease;
      -webkit-animation: rotating 1.5s infinite linear;
    }
  }
  @keyframes rotating {
    0%{
      transform: rotate(0deg);
    }
    100%{
      transform: rotate(360deg);
    }
  }
  &.done{
    opacity: 0;
    visibility: hidden;
  }
}
.section-title{
  display: block;
  position: relative;
  font-size: 32px;
  color: var(--color-text);
  &:before{
    content: '';
    position: absolute;
    bottom: -5px;
    left: 0;
    width: 10%;
    height: 2px;
    background: var(--color-blue);
  }
  &:after{
    content: '';
    position: absolute;
    bottom: -10px;
    left: 0;
    width: 20%;
    height: 1px;
    background: var(--color-green);
  }
}
.block-title{
  font-size: 22px;
  color: var(--color-text);
  font-weight: 700;
}

.btn{
  display: inline-block;
    padding: 15px 20px;
    color: #fff;
    font-size: 18px;
    text-transform: uppercase;
    border: 1px solid #fff;
    background: transparent;
    -webkit-transition: background 200ms;
    transition: background 200ms;
    &:hover{
      background: #215c89;
    }
}
.btn-2{
  display: inline-block;
    padding: 15px 20px;
    color: var(--color-text);
    font-size: 18px;
    text-transform: uppercase;
    border: 1px solid var(--color-text);
    background: transparent;
    -webkit-transition: background 200ms;
    transition: background 200ms;
    &:hover{
      color: #fff;
      background: #215c89;
    }
}

.p-100{
  padding: 50px 15px;
}

// pages
.page{
  min-height: 100vh;
  & .container{
    position: relative;
    min-height: 100vh;
    padding-top: 20px;
    padding-bottom: 50px;
    &:before{
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(to right,transparent,#fff), url(https://st3.depositphotos.com/1157091/13545/v/950/depositphotos_135458158-stock-illustration-abstract-background-medical.jpg);
      background-size: cover;
      opacity: 0.1;
   }
  }
}
.page-title{
  display: block;
  text-align: center;
  padding: 50px 0;
  font-size: 36px;
  color: #fff;
  background: linear-gradient(to bottom,var(--color-text),#2c78b3);
}

// sidebar
.sidebar{
  &-item{
    padding: 0 10px 10px 10px;
    background: #fff;
    box-shadow: 0 2px 2px rgba(0, 0, 0, 0.13);
    margin-bottom: 15px;
  }
  &-title{
    display: block;
    padding: 5px 0;
    text-align: center;
    font-size: 22px;
    color: var(--color-text);
    margin-bottom: 15px;
  }
}