@import "vars";
@import "mixins";
@import "fonts";
@import "settings";

//scroll-to-top
.scroll-to-top {
  position: fixed;
  right: 10px;
  bottom: 10px;
  width: 50px;
  height: 50px;
  border-radius: 5px;
  background-color: var(--color-text);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  opacity: 0;
  visibility: hidden;
  transition: opacity 350ms;
}
.scroll-to-top svg {
  width: 30px;
  height: 30px;
  fill: #fff;
}
.scroll-to-top.show {
  opacity: 0.3;
  visibility: visible;
  &:hover{
    opacity: 1;
  cursor: pointer;
  }
}
// navigation
.nav{
  width: 100%;
  position: relative;
  background: var(--color-text);
  border-bottom: 1px solid var(--color-green);
  & ul.nav-list{
    margin: 0;
    padding: 0;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: start;
        -ms-flex-pack: start;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    & .nav-item{
      position: relative;
     display: inline-block;
      &:not(:last-child){
        // margin-right: 15px;
        &:before{
          content: '';
          position: absolute;
          top: 0;
          right: 0;
          width: 1px;
          height: 100%;
          background: -webkit-gradient(linear, left top, left bottom, from(transparent), color-stop(#fff), to(transparent));
          background: linear-gradient(to bottom, transparent, #fff, transparent);
          opacity: 0.8;
          z-index: 1;
        }
      }
      & a.nav-link{
        position: relative;
        display: inline-block;
        padding: 15px 10px;
        color: #fff;
        font-size: 15px;
        text-transform: uppercase;
        border-top: 2px solid transparent;
        &:hover{
          border-top: 2px solid var(--color-text);
        }
      }
      &.has-child{
        position: relative;
        & i.fa{
          display: inline-block;
          margin-left: 10px;
        }
        & ul.sub-menu{
          position: absolute;
          top: 100%;
          left: 0;
          width: 300px;
          padding: 10px;
          background: #fff;
          display: flex;
          justify-content: flex-start;
          flex-wrap: wrap;
          opacity: 0;
          -webkit-transform: translateY(20px);
          transform: translateY(20px);
          visibility: hidden;
          -webkit-transition: opacity 200ms, -webkit-transform 250ms ease-in-out;
          transition: opacity 200ms, -webkit-transform 250ms ease-in-out;
          transition: transform 250ms ease-in-out, opacity 200ms;
          transition: transform 250ms ease-in-out, opacity 200ms, -webkit-transform 250ms ease-in-out;
          z-index: 10;
          & li{
            display: inline-block;
            margin: 10px;
            & a{
              display: block;
              padding: 5px;
              color: #215c89;
              font-size: 15px;
              text-transform: capitalize;
              background: transparent;
              transition: background 200ms,color 200ms;
              &:hover{
                background: var(--color-text);
                color: #fff;
              }
            }
          }
        }
        &:hover{
          & ul.sub-menu{
            opacity: 1;
          -webkit-transform: translateY(0);
          transform: translateY(0);
          visibility: visible;
          }
        }
        &:nth-child(2) ul.sub-menu{
          width: 700px;
        }
        &:nth-child(3) ul.sub-menu{
          width: 700px;
        }
      }
    }
  }
}
