// ======POPUP==================================================================================
.popup{
   position: fixed;
   top: 0;
   left: 0;
   width: 100%;
   height: 100%;
   background: linear-gradient(to left,#215c89,rgba(5, 5, 5, 0.8)), url(https://www.mediquant.com/wp-content/uploads/2021/01/health-data-stewardship.jpg) no-repeat;
   background-size: cover;
   display: flex;
   justify-content: center;
   align-items: center;
   opacity: 0;
   visibility: hidden;
   transition: opacity 0.5s;
   z-index: 999;
   &__container{
      position: relative;
     width: 90%;
     height: 90%;
     color: #fff;
     background: transparent;
     text-align: center;
     padding: 30px 20px;
     opacity: 0;
     transform: translateY(-20%);
     transition: transform 0.5s;
     & h2{
       display: inline-block;
       margin-bottom: 15px;
     }
     & p{
       font-size: inherit;
       line-height: 1.3;
       margin-bottom: 15px;
     }
     & .popup-inner{
       height: inherit;
       display: flex;
       justify-content: space-around;
       align-items: center;
       & ul.popup-info{
        width: 40%;
        min-height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;
        & li{
          display: flex;
          align-items: center;
          &:not(:last-child){
            margin-bottom: 60px;
          }
          & i{
            display: inline-block;
            padding: 15px;
            width: 100px;
            height: 100px;
            border-radius: 50%;
            display: flex;
            justify-content: center;
            align-items: center;
            color: var(--color-text);
            background: #ccc;
            font-size: 36px;
            margin-right: 10px;
          }
          & span.info-title{
            display: block;
            text-align: left;
            font-size: 18px;
            font-weight: 700;
            color: var(--color-blue);
            margin-bottom: 10px;
          }
        }
      }
     }
     
   }
   &.popup-open{
     opacity: 1;
     visibility: visible;
     transition: opacity 0.5s;
     & .popup__container{
       opacity: 1;
       transform: translateY(0);

     }
   }
   &-close{
      position: absolute;
      top: 10px;
      right: 10px;
      width: 40px;
      height: 40px;
      transition: transform 0.2s;
      &:before,
      &:after{
         content: '';
         position: absolute;
         top: 50%;
         left: 0;
         width: 100%;
         height: 1px;
         background: #fff;
      }
      &:before{
         transform: rotate(45deg);
      }
      &:after{
         transform: rotate(-45deg);
      }
      &:hover{
        transform: scale(1.1);
      }
   }
 }
 .form{
    width: 50%; 
    background: rgba(20, 192, 250, 0.404);
    padding: 15px 20px;
    box-shadow: 0 2px 3px rgba(0, 0, 0, 0.089);
    & label.label{
      display: block;
      width: 100%;
      margin-bottom: 10px;
      & span{
        display: block;
        text-align: left;
        text-transform: uppercase;
        margin-bottom: 5px;
      }
      & input.form-inputs{
        outline: none;
        display: block;
        width: 100%;
        padding: 15px 10px;
        color: #fff;
        background: transparent;
        border: 1px solid #fff;
      }
      &.label-message .textarea{
        outline: none;
        resize: none;
        display: block;
        height: 150px;
        width: 100%;
        padding: 15px 10px;
        color: #fff;
        background: transparent;
        border: 1px solid #fff;
      }
    }
    & input.submit{
      cursor: pointer;
      transition: border 0.2s;
      &:hover{
        color: rgba(245, 245, 245, 0.6);
        border: 1px solid #f5f5f5c5;
      }
    }
 }
 
