// ======ACCORDION==================================================================================

.accordion {
  width: 1000px;
  &__item{
    position: relative;
    margin: 10px 20px;
    & .label{
      position: relative;
      padding: 10px;
      background: var(--color-text);
      color: #fff;
      font-size: 18px;
      cursor: pointer;
      &::before{
        content: '+';
        position: absolute;
        top: 50%;
        right: 20px;
        transform: translateY(-50%);
        font-size: 2em;
      }
    }
    & .content{
      position: relative;
      background: #fff;
      height: 0;
      overflow: hidden;
      opacity: 0;
      line-height: 1.5;
      transition: height 0.5s;
      overflow-y: auto;
    }
    &.active{
      & .label::before{
        content: '-';
      }
      & .content{
        height: 150px;
        padding: 10px;
        opacity: 1;
      }
    }
  }
  
}


